@import "variable";

#footer {
  width: 100%;
  background-color: $color-light-gray;
  .container {
    .container-inner {
      .wrapper {
        .content {
          max-width: 1024px;
          margin-left: auto;
          margin-right: auto;
          padding-left: 22px;
          padding-right: 22px;
          box-sizing: border-box;
          .footer-note {
            padding-top: 34px;
            padding-bottom: 34px;
            text-align: justify;
            font-size: 1em;
            font-family: inherit;
            font-weight: inherit;
            font-weight: inherit;
            line-height: inherit;
            letter-spacing: inherit;
            text-align: inherit;
            line-height: 24px;
            border-bottom: 1px solid $color-line;
          }
          .footer-nav {
            display: flex;
            flex-wrap: nowrap;
            padding-top: 20px;
            width: 100%;
            box-sizing: border-box;
            font-size: 1em;
            font-family: inherit;
            font-weight: inherit;
            font-weight: inherit;
            line-height: inherit;
            letter-spacing: inherit;
            text-align: inherit;
            .column {
              flex-basis: 20%;
              flex-grow: 1;
            }
            .collapse {
              .collapse-header {
                padding-top: 10px;
                padding-bottom: 10px;
                color: $color-dark;
              }

              .collapse-content {
                padding-bottom: 16px;
                transition: all 3s;
              }
            }
            .category {
              a {
                color: $color-text;
              }
              .item {
                padding-bottom: 10px;
              }
            }
          }

          .footer-footer {
            padding-top: 20px;
            padding-bottom: 20px;
            .footer-shop {
              padding-bottom: 17px;
              margin-bottom: 18px;
              border-bottom: 1px solid $color-line;
            }
            .footer-legal {
              display: flex;
              justify-content: space-between;
              padding-top: 5px;
              a {
                color: inherit;
              }
            }
          }
        }
      }
    }
  }
}

// footer breakpoint
@media screen and (max-width: 833px) {
  #footer .container .container-inner .wrapper .content .footer-nav {
    flex-direction: column;
    flex-wrap: nowrap;
    height: auto;
    width: 100%;
    box-sizing: border-box;

    .category .title {
      border-bottom: 1px solid $color-dark;
      padding-top: 10px;
    }

    .collapse {
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid $color-line;
      position: relative;
      .collapse-header {
        padding-bottom: 10px;
        &:hover {
          cursor: pointer;
        }

        &::after {
          content: url("../images/index/angle-down-solid.svg");
          position: absolute;
          right: 0;
          display: inline-block;
          width: 15px;
          height: 15px;
          transition: all 0.5s;
        }
      }

      &.collapsed {
        .collapse-header {
          &::after {
            transform: rotateX(180deg);
          }
        }
        .collapse-content {
          display: block;
        }
      }

      .collapse-content {
        display: none;
        transition: all 0.5s;
        ul li {
          padding: 5px 10px;
        }
      }
    }
  }
}
